import * as React from "react";
import {AddressVo} from "../address/AddressVo";

export interface AddressViewProps {
    address: AddressVo;
}

export class AddressView extends React.Component<AddressViewProps, {}> {
    constructor(props: AddressViewProps) {
        super(props);
    }

    render() {
        const address = this.props.address;

        return (
            <React.Fragment>
                <div>
                    <h1></h1>
                    <p>
                        : {address.zipCode.zipCodeStr}
                    </p>
                    <p>
                        : {address.prefecture.prefectureJisCode} {address.prefecture.prefectureName}
                    </p>
                    <p>
                        : {address.city.cityJisCode} {address.city.cityName}
                    </p>
                    <p>
                        : {address.town.townName}
                    </p>
                </div>
            </React.Fragment>
        );
    }

}
